  font-weight: bold;
  width: 385px;
}
.gv-difesa-suolo-foto-log {
  position: absolute;
  width: 270px;
  left: 0px;
  top: 0px;
  /* margin-left: 10px;
  margin-top: 10px; */
  z-index: 800;
  padding: 10px;
}
.gv-difesa-suolo-foto-log-wrapper {
  width: 400px;
  height: 520px;
}
.gv-difesa-suolo-foto-log-button {
