  padding-bottom: 10px;
}
.gv-difesa-suolo-foto-log-body {
  padding: 15px;
  overflow-y: auto;
  width: 370px;
  height: 450px;
}
.gv-close {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: 0;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  /* background-color: #e94e1b !important; */
  /* background-color: #5b565c !important; */
  color: #ddd;
  float: right;
  font-size: 1rem;
  line-height: 1;
  font-weight: 800;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 3px;
}



