
.gv-list-group {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  background-color: #fff;
  width: 260px;
}
.gv-legend-map-tools {
  position: relative;
  padding-top: 2px;
  padding-bottom: 2px;
  width: 260px;
}
.gv-list-legend-map-item {
  position: relative;
  display: block;
  padding: 0.1rem 0.5rem;
  margin-bottom: -1px;
  border: 1px solid #ddd;
  font-size: 13px;
  font-weight: bold;
}
.gv-list-legend-map-item li {
  list-style-type: none;
}
.gv-legend-map-tools-button {
  position: relative;
  right: 0;
  bottom: 0;
  font-size: 14px;
  padding: 2px 2px;
}
.gv-legend-map-tools span {
  font-family: 'Raleway', Arial, sans-serif !important;
  font-size: 12px;
  font-weight: bold;
}
#gv-legend-maps {
  width: 260px;
  max-height: 400px;
  cursor: default;
  overflow: hidden;
}
#gv-legend-maps:hover {
  overflow-y: scroll;
}
@media (max-height: 500px) {
#gv-legend-maps {
    overflow-y: scroll;
    max-height: 200px;
}
}
.el-input__suffix {
  right: 5px;
  transition: all 0.3s;
  pointer-events: none;
  color: #24386c;
}
.el-table .cell {
  word-break: normal !important;
  line-height: 15px !important;
}



